.pledge-btn {
  margin-top: 35px;
  margin-left: 20px;
}
.header-btn {
  color: #6236FF;
  font-size: 16px;
  cursor: pointer;
}
.mymine-body .mymine {
  width: 274px;
  height: 265px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-left: 10px;
  margin-top: 10px;
  padding: 10px 20px;
}
.mymine-body .mymine .serial-name {
  height: 17px;
  font-size: 14px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.85);
  line-height: 16px;
}
.mymine-body .mymine .serial-num {
  height: 17px;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.25);
  line-height: 16px;
  margin-bottom: 25px;
}
.mymine-body .mymine .mine-name {
  width: 113px;
  height: 16px;
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  line-height: 16px;
}
.mymine-body .mymine .mine-num {
  width: 77px;
  height: 19px;
  font-size: 19px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.65);
  line-height: 19px;
  margin-bottom: 15px;
}
.mymine-body .mymine .mine-back {
  padding: 0 4px;
  height: 15px;
  background: #F9B82E;
  border-radius: 4px;
  font-size: 10px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.85);
  line-height: 15px;
  margin-left: 5px;
  text-align: center;
}
.mymine-body .mymine .mine-btn {
  height: 22px;
  line-height: 22px;
  border-radius: 14px;
  font-size: 12px;
}
.mymine-body .mymine .mine-dxAddress {
  font-size: 10px;
  padding-top: 20px;
  color: #eb1313;
}
.mymine-body .mymine-det {
  margin-left: 10px;
}
.mymine-body .serial-num1 {
  height: 17px;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.25);
  line-height: 16px;
  margin-bottom: 5px;
}
.mymine-body .last {
  margin-bottom: 15px;
}
.mymine-body .contents {
  font-size: 12px;
}
.mymine-header {
  width: 100%;
  height: 115;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  padding-bottom: 20px;
}
@media only screen and (max-width: 768px) {
  .pledge-btns {
    margin-top: 15px;
    margin-left: 35px;
  }
  .mymine-body .mymine {
    width: 95%;
    margin-left: 10px;
    margin-top: 10px;
  }
  .mymine-body .mymine .mine-btn {
    margin-left: 10px;
  }
  .header-contents {
    height: 93px;
    width: 100%;
    margin-top: 0;
    border: 1px solid #eeeeee;
    border-radius: 8px ;
    padding-top: 20px;
    margin-bottom: 20px;
  }
  .header-lefts {
    margin-left: 0%;
  }
  .all-left {
    margin-left: 15px;
  }
  .all-left .all-left-num {
    font-size: 20px;
    color: #0091FF;
    font-weight: 800;
    margin-top: 10px;
  }
}
