.voteDialogWrapper {
  width: auto;
  display: inline-block;
}
@media screen and (max-width: 750px) {
  .voteDialogWrapper {
    font-size: 12px;
    font-weight: bold;
    color: #000000;
    line-height: 12px;
  }
}
@media screen and (min-width: 750px) {
  .voteDialogWrapper {
    font-size: 16px;
    font-weight: bold;
    color: #000000;
    line-height: 12px;
  }
  .voteDialogWrapper .ant-table-tbody > tr:last-child > td {
    border-bottom: none;
  }
}
.voteDialogWrapper .ant-card {
  border-radius: 11px;
}
@media screen and (max-width: 1200px) {
  .voteDialogWrapper .ant-card-bordered {
    border: none;
  }
}
@media screen and (max-width: 750px) {
  .voteDialogWrapper .delegateItem {
    border-radius: 10px 10px 10px 10px;
  }
  .voteDialogWrapper .delegateItem .delegateItemTop {
    border-radius: 10px 10px 0px 0px;
  }
  .voteDialogWrapper .ant-table-tbody > tr > td {
    border-bottom: none;
  }
}
.voteDialogWrapper .ant-card-body {
  padding: 0px;
}
.voteDialogWrapper .ant-table-row td {
  vertical-align: middle !important;
}
.voteDialogWrapper .cardTitle {
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 25px);
  justify-content: center;
  align-content: baseline;
  justify-items: center;
  align-items: center;
  grid-gap: 20px;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
}
.voteDialogWrapper .cardTitle .cardbutton {
  margin: 0px 5px;
  width: 88px;
  height: 26px;
  border-radius: 4px;
  padding: 6px 16px;
  border: 1px solid #ffffff;
  font-size: 14px;
  font-weight: 500;
  color: #ffffff;
  line-height: 14px;
  background-color: #0279ff;
}
@media screen and (max-width: 750px) {
  .voteDialogWrapper .cardTitle {
    font-size: 12px;
  }
  .voteDialogWrapper .cardTitle .cardbutton {
    margin: 0 3px;
    width: 64px;
    height: 26px;
    border-radius: 4px;
    padding: 6px 8px;
    border: 1px solid #ffffff;
    font-size: 12px;
    font-weight: 500;
    color: #ffffff;
    line-height: 14px;
    background-color: #0279ff;
  }
  .voteDialogWrapper .cardTitle .cardbutton[disabled] {
    color: rgba(0, 0, 0, 0.25);
    background-color: #f5f5f5;
    border-color: #d9d9d9;
    text-shadow: none;
    box-shadow: none;
    cursor: not-allowed;
  }
}
@media screen and (min-width: 375px) {
  .voteDialogWrapper .cardTitle .cardbutton {
    margin: 0 3px;
    width: 80px;
    height: 26px;
    border-radius: 4px;
    padding: 6px 12px;
    border: 1px solid #ffffff;
    font-size: 13px;
    font-weight: 500;
    color: #ffffff;
    line-height: 14px;
    background-color: #0279ff;
  }
}
@media screen and (min-width: 750px) {
  .voteDialogWrapper .cardTitle .cardbutton {
    margin: 0 10px;
    width: 80px;
    height: 26px;
    border-radius: 4px;
    padding: 6px 12px;
    border: 1px solid #ffffff;
    font-size: 14px;
    font-weight: 500;
    color: #ffffff;
    line-height: 14px;
    background-color: #0279ff;
  }
  .voteDialogWrapper .cardTitle .cardbutton[disabled] {
    color: rgba(0, 0, 0, 0.25);
    background-color: #f5f5f5;
    border-color: #d9d9d9;
    text-shadow: none;
    box-shadow: none;
    cursor: not-allowed;
  }
}
.voteDialogWrapper .cardTitle .cardTime {
  font-size: 20px;
  font-weight: 500;
  color: #ffffff;
  line-height: 20px;
}
.voteDialogWrapper .normalSort {
  vertical-align: middle;
  font-size: 15px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
  line-height: 12px;
  min-width: 25px;
}
.voteDialogWrapper .spanWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.voteDialogWrapper .normalSort {
  vertical-align: middle;
  font-size: 24px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
  line-height: 12px;
}
.voteDialogWrapper .modalTitletovote .leftIcon {
  display: inline-block;
  width: 4px;
  height: 20px;
  background: #000000;
  border-radius: 4px;
}
.voteDialogWrapper .modalTitletovote .rightText {
  margin-left: 14.6px;
  display: inline-block;
  width: auto;
  height: 48px;
  font-size: 24px;
  font-weight: bold;
  color: #000000;
  line-height: 48px;
}
.voteDialogWrapper .modalFootertovote .modalFootertovoteEext {
  display: inline-block;
  width: 35px;
  height: 16px;
  font-size: 16px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.25);
  line-height: 16px;
}
.voteDialogWrapper .modalFootertovote .modalFootertovoteConfirm {
  margin-left: 16px;
  display: inline-block;
  width: 125px;
  height: 48px;
  background: #1976d1;
  padding: 16px 30px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  line-height: 16px;
}
.voteDialogWrapper .modalFootertovote .modalFootertovoteConfirm[disabled] {
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border-color: #d9d9d9;
  text-shadow: none;
  box-shadow: none;
  cursor: not-allowed;
}
.voteDialogWrapper .inputWrapperVote .ant-input {
  height: 55px;
}
.voteDialogWrapper .inputWrapperVote .ant-select-selection--multiple .ant-select-selection__rendered {
  height: 55px;
}
.voteDialogWrapper .inputWrapperVote .ant-form-item-label {
  line-height: 14px !important;
}
.voteDialogWrapper .modalConfirm {
  padding: 24px 32px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  background: #f2f2f2;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.voteDialogWrapper .modalResult {
  padding: 24px 32px;
  width: 100%;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.voteDialogWrapper .ant-modal-body {
  padding: 10px !important;
}
.voteDialogWrapper .ant-modal-header {
  border-bottom: none;
}
.voteDialogWrapper .ant-modal-footer {
  border-top: none;
}
.voteDialogWrapper .ant-table-tbody > tr > td {
  padding: 16px 2px;
}
.voteDialogWrapper .TextVote {
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
}
.voteDialogWrapper .mobile-delegate-list {
  text-align: right;
  margin-top: 20px;
}
.voteDialogWrapper .mobile-delegate-list .nameAndsort {
  display: flex;
  justify-content: space-between;
}
.voteDialogWrapper .mobile-delegate-list .nameAndsort .nameonly {
  display: flex;
  align-items: center;
  margin-left: 10px;
}
.voteDialogWrapper .mobile-delegate-list .ant-table-thead > tr > th {
  background: #fff;
}
.voteDialogWrapper .mobile-delegate-list .delegateItem {
  width: 100%;
  margin-bottom: 16px;
  box-shadow: #e3e2e2 0px 1px 2px;
}
.voteDialogWrapper .mobile-delegate-list .delegateItem .delegateItemTop {
  width: 100%;
  height: 77px;
  background-color: #f6f8fa;
  display: flex;
  padding: 0px 16px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.voteDialogWrapper .mobile-delegate-list .delegateItem .delegateItemBottom .addpadding {
  display: inline-block;
  padding: 0px 16px;
}
@media screen and (max-width: 1200px) {
  .voteDialogWrapper .mobile-delegate-list .delegateItem .delegateItemBottom .spanWrapper {
    display: flex;
    justify-content: start;
    padding: 0px 16px;
    align-items: center;
  }
}
