  .RenderMachineLabel {
    padding: 0 10px;
    display: grid;
    justify-items: start;
    align-items: start;
    grid-template-columns: 100%;
    grid-template-rows: 14px 14px 14px;
    grid-row-gap: 11px;
    line-height: 14px;
  }
  .RenderMachineLabel .RenderMachineLabelTop {
    font-size: 18px;
    font-weight: bold;
    color: #000000;
    line-height: 18px;
  }
  .RenderMachineLabel .RenderMachineLabelTop .colorRequire {
    color: red;
  }
  .RenderMachineLabel .RenderMachineLabelBottom {
    font-size: 12px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
    line-height: 14px;
  }
  .RenderNodeNameLabel {
    padding: 0 10px;
    display: grid;
    justify-items: start;
    align-items: start;
    grid-template-columns: 1fr;
    grid-template-rows: 14px 14px;
