.detailed .personal-title {
  font-size: 20px;
  font-weight: bold;
  color: #000;
  margin-bottom: 20px;
}
.detailed .detailed-goback {
  height: 18px;
  font-size: 14px;
  color: #0091ff;
  line-height: 18px;
  cursor: pointer;
  margin-bottom: 20px;
}
.detailed .all-money,
.detailed .all-pledge {
  clear: both;
  width: 868px;
  height: 109px;
  border-radius: 0px 0px 8px 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-bottom: 20px;
}
.detailed .all-money .all-left,
.detailed .all-pledge .all-left {
  margin-left: 40px;
  margin-top: 20px;
}
.detailed .all-money .all-left .all-left-num,
.detailed .all-pledge .all-left .all-left-num {
  font-size: 20px;
  color: #0091FF;
  font-weight: 800;
  margin-top: 10px;
}
.detailed .qu-btn {
  color: #0091ff;
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .detailed .all-money {
    width: 100%;
    height: 109px;
  }
  .detailed .all-pledge {
    width: 100%;
    height: 209px;
  }
  .detailed .all-pledge .all-left {
    margin-left: 10px;
    margin-top: 20px;
  }
  .detailed .all-pledge .all-left .all-left-num {
    font-size: 20px;
    color: #0091FF;
    font-weight: 800;
    margin-top: 10px;
  }
}
