.voteResult .ant-table-tbody > tr > td {
  vertical-align: middle;
}
.voteResult .weight {
  font-size: 20px;
  font-weight: 100;
}
.voteResult .exitAll {
  width: 86px;
  height: 28px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #e02020;
  line-height: 16px;
}
.voteResult .personal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
}
.voteResult .normalSort {
  vertical-align: middle;
  font-size: 24px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
  line-height: 12px;
}
@media only screen and (max-width: 600px) {
  .voteResult .testWrapper {
    padding: 10px 15px;
    display: grid;
    background-color: #f0f2f5;
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 20px;
    row-gap: 20px;
    grid-column-gap: 20px;
    column-gap: 20px;
    justify-items: center;
    align-items: center;
    justify-content: space-around;
    align-content: space-around;
    grid-template-areas: "header . header" "main main sidebar" "footer footer footer";
  }
  .voteResult .testWrapper .test-item {
    width: 100%;
    height: 200px;
    background-color: #fff;
    border: 1px solid #eeeeee;
    border-radius: 8px;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    justify-items: start;
    align-items: center;
    padding: 5px;
  }
  .voteResult .testWrapper .test-item .buttonLeft {
    display: inline-block;
    width: 20px;
    padding: 3px 6px;
  }
}
