.header-contents {
  height: 93px;
  width: 100%;
  margin-top: 0;
  border: 1px solid #eeeeee;
  border-radius: 8px ;
  padding-top: 20px;
  margin-bottom: 20px;
}
.header-lefts {
  margin-left: 0%;
}
.all-left {
  margin-left: 15px;
}
.all-left .all-left-num {
  font-size: 20px;
  color: #0091FF;
  font-weight: 800;
  margin-top: 10px;
}
.all-money,
.all-pledge {
  width: 853px;
  min-height: 109px;
  height: auto;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  margin-bottom: 20px;
}
.all-money .all-left,
.all-pledge .all-left {
  margin-left: 40px;
}
.all-money .all-left .all-left-num,
.all-pledge .all-left .all-left-num {
  font-size: 20px;
  color: #0091FF;
  font-weight: 800;
  margin-top: 10px;
}
.supernodenum {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.supernodenum .flexitem {
  flex: 1 0 auto;
}
